.blog {
  position: relative;
  font-family: $font-basic;
  font-size: 16px;
  line-height: 21px;
  font-weight: 500;
  text-align: center;
  color: $basic-black;
  background-color: $basic-white;
}

.blog__wrapper {
  @include wrapper;
  padding: 65px 0 19px 0;
  align-items: center;
  display: flex;
  flex-direction: column;

  @media (max-width: $tablet-width-only) {
    padding: 64px 0 20px 0;
  }

  @media (max-width: $mobile-width-only) {
    padding: 59px 0 20px 0;
  }

  h2 {
    @include reset-list;
    margin: 46px 0 19px 0;
    font-family: $font-special;
    font-size: 34px;
    line-height: 44px;
    font-weight: 700;
    letter-spacing: 0.03em;
    color: $basic-zeus;

    @media (max-width: $tablet-width-only) {
      margin: 0 0 19px 0;
    }
  }
}

.blog__link {
  @include reset-list;
  padding: 0 0 3px 0;
  font-family: $font-basic;
  font-size: 16px;
  line-height: 21px;
  font-weight: 500;
  color: $basic-zeus;
  border-bottom: 1px solid $basic-muesli;

  &:hover,
  &:focus {
    color: $basic-muesli;
  }

  @media (max-width: $tablet-width-only) {
    padding: 0 0 4px 0;
  }
}
